<template>
  <div class="sign_box">
    <el-form ref="form" :model="user" >
      <p class="form_title"></p>


      <el-form-item label="" label-width="100px">
        <el-input v-model="user.name" @focus="alert('', '')"></el-input>
      </el-form-item>


      <el-form-item label="" label-width="100px">
        <el-input v-model="user.username" @focus="alert('', '')"></el-input>
      </el-form-item>



      <el-form-item label="" label-width="100px">
        <el-input v-model="user.password1" type="password" @focus="alert('','3-15')"></el-input>
      </el-form-item>

      <el-form-item label="" label-width="100px">
        <el-input v-model="user.password2" type="password" @focus="alert('','')"></el-input>
      </el-form-item>

      <el-form-item label="" label-width="100px">
        <el-radio-group v-model="user.department">
          <el-radio-button label=""></el-radio-button>
          <el-radio-button label=""></el-radio-button>
          <el-radio-button label=""></el-radio-button>
        </el-radio-group>
      </el-form-item>


      <el-form-item style="text-align:center; ">
        <el-button type="primary" @click="goLoginIn"></el-button>
      </el-form-item>


    </el-form>
  </div>
</template>

<script>
import url from '../url.js'
import axios from 'axios'
export default {
  name: 'sign_box',
  data () {
    return {
      user: {
          username: '',
          name: '',
          password1: '',
          password2: '',
          department: '',
      },
      signUrl: url.signUrl
    }
  },
  methods: {
    goLoginIn() {
      let user = this.user
      let ps1 = user.password1
      let ps2 = user.password2
      let name = user.name
      let username = user.username
      let department = user.department
      let signUrl = this.signUrl
      let reg = /^[0-9a-zA-Z]+$/

      if(!name.length) {
        this.$message.error('')
        return
      }

      if(!username.length) {
        this.$message.error('')
        return
      }

      if( !(ps1 == ps2) ) {
        this.$message.error('')
        ps1 = ''
        ps2 = ''
        return
      }

      if((ps1.length < 3) || (ps1.length > 15) ) {
        this.$message.error('3-15')
        ps1 = ''
        ps2 = ''
        return
      }

      if(!department.length) {
        this.$message.error('')
      }

      if(department == '') department = 1
      if(department == '') department = 2
      if(department == '') department = 3

      axios.post(signUrl, {
        user_name: username,
        nickname: name,
        department_id: department,
        password: ps1
      })
      .then(e => {
        console.log(e)
        if(e.data.code === 1 ) {
          this.$message.error(e.data.message)
          for(let i in this.user) {
            user[i] = ''
          }
        }
        else{
          this.$message(e.data.message)
          this.$emit('goLoginIn', true)

          for(let i in this.user) {
            user[i] = ''
          }
        }

      })

    },
    alert(title, message) {
      this.$notify({title, message})
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sign_box  {
  border:1px solid #909399;
  margin:auto;
  border-radius: 5px;
  padding:30px;
  background: white;
  width:40%;
  height:60%;
  margin: auto;
  position: absolute;
  top:0;
  left:0;
  right:0;
  bottom:0;
}
.form_title{
  text-align: center;
  margin:10px 0;
}
</style>



