<script>
import axios from 'axios'
import $ from 'jquery'
import {saveReservoUrl, updateResourceUrl, downItemExcelUrl} from '../url'
export default {
  name: 'travel_itinerary',
  data () {
    return {
      ticket: [],
      bus_ticket: [],
      chartered: [],
      schedule: [],

      isExist: false,


      options: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      optionsCar: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ]
    }
  },
  created() {

  },
  methods: {
    downExcel() {
      window.location.href = downItemExcelUrl+ this.$route.params.mess.id
    },
    error(msg) {
      this.$message.error(msg)
    },
    getData() {
      let id = this.$route.params.mess.id
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"get",
            url: saveReservoUrl + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result)
            },
            error(result) {
              reject(result)
            }
          })
      })
    },
    saveAjax() {
      let ticket = this.ticket 
      let bus_ticket = this.bus_ticket
      let schedule = this.schedule
      let chartered = this.chartered
      // ticket 
      for(let item of ticket) {
        if(!(item.name.length && item.pdate && item.pname.length && item.ptype.length )) {
          this.error('*')
          return 
        }
        if(!/^[0-9]+$/.test(item.pmoney)) {
          this.error('')
          return
        }
      }
      // bus_ticket 
      for(let item of bus_ticket) {
        if(!(item.from.length && item.pname.length && item.psection.length && item.ptype.length && item.towhere.length)) {
          this.error('*')
          return 
        }
        if(!/^[0-9]+$/.test(item.pmoney)) {
          this.error('')
          return
        }
      }
      // other
      for(let item of schedule) {
        if(!(item.name.length && item.scheduledproject.length)) {
          this.error('*')
          return 
        }
        if(!/^[0-9]+$/.test(item.pmoney)) {
          this.error('')
          return
        }
      }

      let data = {
        ticketEntities: this.ticket,
        busTicketEntities: this.bus_ticket,
        charteredEntities: this.chartered,
        scheduleList: this.schedule 
      }
      let id = this.$route.params.mess.id
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            data: JSON.stringify(data),
            url: updateResourceUrl + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result)
            },
            error(result) {
              reject(result)
            }
          })
      })
    },
    saveTravel() {
      let _this = this
      this.saveAjax().then(e => {
        if(e.code === 200) {
          _this.$message.success(e.message)
        }
        else{
          _this.$message.error(e.message)
        }
      })
    },

    // 
    ticketInsert() {
      this.ticket.push({
        name: '',
        pnumber: '',
        pname: '',
        ptype: '',
        pdate: '',
        pmoney: '',
        success: '',
        truemoney: '',
        profit: '',
        getway: '',
      })
    },
    bus_ticketInsert() {
      this.bus_ticket.push({
        from: '',
        towhere: '',
        pname: '',
        ptype: '',
        psection: '',
        getplace: '',
        getdate: '',
        pmoney: '',
        success: '',
        truemoney: '',
        profit: ''
      })
    },
    charteredInsert() {
      let _this = this
      setTimeout( () => {
        _this.$notify({
          title: '',
          message: '',
          duration: 0
        })
      },500)
      setTimeout( () => {
        _this.$notify({
          title: '',
          message: '',
          duration: 0
        })
      },1000)

      setTimeout( () => {
        _this.$notify({
          title: '',
          message: '',
          duration: 0
        })
      },1500)
      
      this.chartered.push({
        type: '',
        pname: '',
        planenumber: '',
        ptype: '',
        date: '',
        pdate: '',
        bagnumber: '',
        tohotel: '',
        card: '',
        pmoney: '',
        truemoney: '',
        profit: '',
        carplace: '',
        caretype: '',
        stopplace: '',
        endplace: '',
        enddate: ''
      })
    },
    scheduleInsert() {
      this.schedule.push({
        name: '',
        scheduledproject: '',
        scheduledate: '',
        pmoney: '',
        success: '',
        truemoney: '',
        profit: ''
      })
    },
    scheduleDel(i) {
      this.schedule.splice(i, 1)
    },
    ticketDel(i) {
      this.ticket.splice(i, 1)
    },
    bus_ticketDel(i) {
      this.bus_ticket.splice(i, 1)
    },
    charteredDel(i) {
      this.chartered.splice(i, 1)
    },
    getUTCTime(str) {
      var str = new Date(str);
      var utc = str.toUTCString();
          return utc;
    }
  },
  created() {

    let _this = this 
    this.getData().then(e => {
      //  ticket: [],
      // bus_ticket: [],
      // chartered: [],
      // schedule: [],
      let data = e.message 
      let isNull = false
      for(let i in data) {
        if(data[i].length) isNull = true
      }
      if(isNull) {
        _this.isExist = true
        let msg = e.message 
        for(let i of msg.busTicketEntities) {
          i.getdate = _this.getUTCTime(i.getdate)
        }
        for(let i of msg.charteredEntities) {
          i.date = _this.getUTCTime(i.date)
          i.enddate = _this.getUTCTime(i.enddate)
          i.pdate = _this.getUTCTime(i.pdate)
        }
        for(let i of msg.scheduleList) {
          i.scheduledate = _this.getUTCTime(i.scheduledate)
        }
        for(let i of msg.ticketEntities) {
          i.pdate = _this.getUTCTime(i.pdate)
        }

        _this.ticket = msg.ticketEntities
        _this.bus_ticket = msg.busTicketEntities
        _this.schedule = msg.scheduleList
        _this.chartered = msg.charteredEntities
      }
    })
  }
}
</script>

